import React from 'react';
import PropTypes from 'prop-types';
import {
  ContentBox,
  ContentBoxName,
  ContentBoxProperty,
  ChangePasswordEmailButton,
  CategoryBox,
} from './SettingsMainContentBox.style';

const convertUserRole = (role) => {
  if (!role) return;
  if (role === 'ROLE_USER') {
    return 'Uytkownik';
  } else if (role === 'ROLE__MODERATOR') {
    return 'Moderator';
  } else if (role === 'ROLE_ADMIN') {
    return 'Administrator';
  }
};

const SettingsMainContentBox = ({
  currentUser: { role, email },
  openModal,
  setModalType,
}) => {
  const openEmailChange = () => {
    setModalType('CHANGE_EMAIL');
    openModal();
  };

  const openPasswordChange = () => {
    setModalType('CHANGE_PASSWORD');
    openModal();
  };

  return (
    <ContentBox>
      <CategoryBox>
        <ContentBoxName>Email</ContentBoxName>
        <ContentBoxProperty>{email}</ContentBoxProperty>
        <ChangePasswordEmailButton onClick={openEmailChange}>
          Zmie email
        </ChangePasswordEmailButton>
      </CategoryBox>

      <CategoryBox>
        <ContentBoxName>Haso</ContentBoxName>
        <ChangePasswordEmailButton onClick={openPasswordChange}>
          Zmie haso
        </ChangePasswordEmailButton>
      </CategoryBox>

      <CategoryBox>
        <ContentBoxName>Rola</ContentBoxName>
        <ContentBoxProperty>{convertUserRole(role)}</ContentBoxProperty>

      </CategoryBox>
    </ContentBox>
  );
};

SettingsMainContentBox.propTypes = {
  currentUser: PropTypes.object,
  role: PropTypes.string,
  email: PropTypes.string,
  openModal: PropTypes.func,
  setModalType: PropTypes.func,
};

export default SettingsMainContentBox;
