import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { AppContext } from 'store/Store';
import { ReactComponent as PersonIcon } from 'assets/icons/personIcon.svg';
import {
  UserSettingsWrapper,
  SettingsHeader,
  HeaderUsername,
  HeaderUserImage,
  NoUserMessage,
  HeaderUserWrapper,
  PageName,
} from './UserSettings.styles';
import SettingsMainContentBox from 'components/organisms/SettingsMainContentBox/SettingsMainContentBox';
import SettingsModals from 'components/organisms/SettingsModals/SettingsModals';
import SlideInfo from 'components/atoms/SlideInfo/SlideInfo';

const UserSettings = (props) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState('');
  const [isSliderInfoInitiate, setIsSliderInfoInitiate] = useState(false);

  const { currentUser, clearFormInputs, message, typeOfMessage } = useContext(
    AppContext
  );

  const closeModal = () => {
    setIsModalOpen(false);
    clearFormInputs();
  };
  const openModal = () => setIsModalOpen(true);
  const resetInfoSlider = () => setIsSliderInfoInitiate(false);
  const initiateInfoSlider = () => setIsSliderInfoInitiate(true);

  return (
    <>
      <UserSettingsWrapper>
        {currentUser ? (
          <>
            <SettingsHeader>
              <PageName>Ustawienia</PageName>
              <HeaderUserWrapper>
                <HeaderUserImage>
                  <PersonIcon />
                </HeaderUserImage>
                <HeaderUsername>{currentUser.username}</HeaderUsername>
              </HeaderUserWrapper>
            </SettingsHeader>
            <SettingsMainContentBox
              setModalType={setModalType}
              openModal={openModal}
              currentUser={currentUser}
            />
          </>
        ) : (
          <NoUserMessage>Nie jeste zalogowany</NoUserMessage>
        )}
        <SettingsModals
          modalType={modalType}
          isOpen={isModalOpen}
          handleOnClose={closeModal}
          initiateInfoSlider={initiateInfoSlider}
        />
      </UserSettingsWrapper>
      <SlideInfo
        message={message}
        restartSliderState={resetInfoSlider}
        infoType={typeOfMessage}
        isInitiated={isSliderInfoInitiate}
      />
    </>
  );
};

UserSettings.propTypes = {};

export default UserSettings;
