import React, { useRef, useState, useContext } from 'react';
import PropTypes from 'prop-types';
import MenuTrigger from 'components/atoms/MenuTrigger/MenuTrigger';
import {
  MenuWrapper,
  MenuWrapperBack,
  CloseMenuButton,
  LoginLink,
  LoginLinkArrow,
  Avatar,
} from './Menu.styles';
import {
  menuOnAnimation,
  menuOffAnimation,
} from 'assets/animations/animations';
import { ReactComponent as CloseIcon } from 'assets/icons/closeIcon.svg';
import { ReactComponent as LoginIcon } from 'assets/icons/loginIcon.svg';
import { ReactComponent as PersonIcon } from 'assets/icons/personIcon.svg';
import { logout } from 'helpers/auth.service';
import { AppContext } from 'store/Store';
import MenuLinksList from 'components/molecules/MenuLinksList/MenuLinkList';
import LoginAndRegister from 'components/organisms/LoginAndRegister/LoginAndRegister';
import SlideInfo from 'components/atoms/SlideInfo/SlideInfo';

const Menu = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [message, setMessage] = useState('');
  const [isInfoSliderInitiated, setIsInfoSliderInitiated] = useState(false);

  let menuWrapperBack = useRef(null);
  let menuWrapper = useRef(null);
  let menuCloseButton = useRef(null);

  const { currentUser, setCurrentUser } = useContext(AppContext);

  const initiateSlider = () => setIsInfoSliderInitiated(true);
  const restartSliderState = () => setIsInfoSliderInitiated(false);

  const handleOnCloseModal = () => setIsModalOpen(false);

  const changeMenuButtonPosition = () =>
    setIsMenuOpen((prevState) => !prevState);

  const menuOn = () => {
    changeMenuButtonPosition();
    menuOnAnimation(menuWrapperBack, menuWrapper, menuCloseButton);
  };

  const menuOff = () => {
    changeMenuButtonPosition();
    menuOffAnimation(menuCloseButton, menuWrapper, menuWrapperBack);
  };

  const goLoginHandler = () => {
    setIsModalOpen(true);
    menuOff();
  };

  const logOut = () => {
    logout();
    setCurrentUser(null);
    setMessage('Pomylnie wylogowano');
    initiateSlider();
  };

  return (
    <>
      <MenuTrigger isMenuOpen={isMenuOpen} onClick={menuOn} />
      <MenuWrapperBack ref={(el) => (menuWrapperBack = el)}>
        <MenuWrapper ref={(el) => (menuWrapper = el)}>
          {currentUser ? (
            <LoginLink>
              <Avatar>
                <PersonIcon />
              </Avatar>
              {currentUser.username}
            </LoginLink>
          ) : (
            <LoginLink>Niezalogowany</LoginLink>
          )}
          <MenuLinksList menuOff={menuOff} />
          {currentUser ? (
            <LoginLink onClick={logOut}>
              <LoginIcon />
              Wyloguj si
              <LoginLinkArrow />
            </LoginLink>
          ) : (
            <LoginLink onClick={goLoginHandler}>
              <LoginIcon />
              Zaloguj si
              <LoginLinkArrow />
            </LoginLink>
          )}
        </MenuWrapper>
        <CloseMenuButton ref={(el) => (menuCloseButton = el)} onClick={menuOff}>
          <CloseIcon />
        </CloseMenuButton>
        <LoginAndRegister
          isModalOpen={isModalOpen}
          handleOnCloseModal={handleOnCloseModal}
        />
      </MenuWrapperBack>
      <SlideInfo
        message={message}
        restartSliderState={restartSliderState}
        isInitiated={isInfoSliderInitiated}
      />
    </>
  );
};

Menu.propTypes = {};

export default Menu;
