import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { Form, ValidationMessage } from './RegisterForm.styles';
import Input from 'components/atoms/Input/Input';
import Button from 'components/atoms/Button/Button';
import { AppContext } from 'store/Store';
import { register, login, getCurrentUser } from 'helpers/auth.service';
import { validateRegistration } from 'helpers/loginValidation';

const RegisterForm = ({ closeLoginBox }) => {
  const [message, setMessage] = useState('');

  const {
    username,
    usernameHandler,
    password,
    passwordHandler,
    email,
    emailHandler,
    clearFormInputs,
    setCurrentUser,
  } = useContext(AppContext);

  const signUpHandler = async (e) => {
    e.preventDefault();
    await register(email, password, username);
    await login(username, password);
    await setCurrentUser(getCurrentUser());
    clearFormInputs();
    closeLoginBox();

    //validateRegistration(username, password, email, setMessage);
  };

  return (
    <Form onSubmit={signUpHandler}>
      <Input
        value={username}
        onChange={usernameHandler}
        placeholder="Nazwa uytkownika"
        type="text"
      />
      <Input
        value={password}
        onChange={passwordHandler}
        placeholder="Haso"
        type="password"
      />
      <Input
        value={email}
        onChange={emailHandler}
        placeholder="Email"
        type="email"
      />
      {message && <ValidationMessage>{message}</ValidationMessage>}
      <Button type="submit">ZAREJESTRUJ SI</Button>
    </Form>
  );
};

RegisterForm.propTypes = {
  closeMenu: PropTypes.func,
};

export default RegisterForm;
