import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import Modal from 'components/atoms/Modal/Modal';
import { ModalWrapper } from './SettingsModals.style';
import ExitButton from 'components/atoms/ExitButton/ExitButton';
import Input from 'components/atoms/Input/Input';
import Button from 'components/atoms/Button/Button';
import StyledLabel from 'components/atoms/StyledLabel/StyledLabel';
import { changeEmail, changePassword } from 'helpers/user.service';
import { getCurrentUser } from 'helpers/auth.service';
import { AppContext } from 'store/Store';

const SettingsModals = ({
  isOpen,
  handleOnClose,
  modalType,
  initiateInfoSlider,
}) => {
  const {
    currentUser,
    email,
    emailHandler,
    password,
    passwordHandler,
    setCurrentUser,
    newPassword,
    newPasswordHandler,
    clearFormInputs,
    setMessage,
    setTypeOfMessage,
  } = useContext(AppContext);

  const handleChangingEmailSucces = () => {
    setCurrentUser(getCurrentUser());
    clearFormInputs();
    setMessage('Pomylnie zmieniono email!');
    setTypeOfMessage('SUCCESS');
    handleOnClose();
    initiateInfoSlider();
  };
  const handleChangingEmailError = () => {
    setMessage('Niepoprawne haso!');
    setTypeOfMessage('ERROR');
    initiateInfoSlider();
  };
  const handleChangingPasswordSucces = () => {
    clearFormInputs();
    setMessage('Pomylnie zmieniono haso!');
    setTypeOfMessage('SUCCESS');
    handleOnClose();
    initiateInfoSlider();
  };
  const handleChangingPasswordError = () => {
    setMessage('Niepoprawne haso!');
    setTypeOfMessage('ERROR');
    initiateInfoSlider();
  };

  const changeUserEmail = async () => {
    await changeEmail(
      email,
      password,
      currentUser.username,
      handleChangingEmailSucces,
      handleChangingEmailError
    );
  };

  const changeUserPassword = async () => {
    await changePassword(
      newPassword,
      password,
      currentUser.username,
      handleChangingPasswordSucces,
      handleChangingPasswordError
    );
  };

  return (
    <Modal
      shouldBeClosedOnOutsideClick={true}
      isOpen={isOpen}
      handleOnClose={handleOnClose}
    >
      {modalType === 'CHANGE_EMAIL' ? (
        <ModalWrapper>
          <ExitButton onClick={handleOnClose} />
          <StyledLabel>
            Nowy email:
            <Input
              value={email}
              onChange={emailHandler}
              placeholder="example@xx.com"
              marginTop="1em"
              type="email"
            />
          </StyledLabel>
          <StyledLabel>
            Haso:
            <Input
              value={password}
              onChange={passwordHandler}
              placeholder="password"
              marginTop="1em"
              type="password"
            />
          </StyledLabel>
          <Button onClick={changeUserEmail}>Zmie</Button>
        </ModalWrapper>
      ) : (
        <ModalWrapper>
          <ExitButton onClick={handleOnClose} />
          <StyledLabel>
            Nowe haso:
            <Input
              value={newPassword}
              onChange={newPasswordHandler}
              placeholder="new password"
              marginTop="1em"
              type="password"
            />
          </StyledLabel>
          <StyledLabel>
            Powtrz haso:
            <Input
              value={newPassword}
              onChange={newPasswordHandler}
              placeholder="new password"
              marginTop="1em"
              type="password"
            />
          </StyledLabel>
          <StyledLabel>
            Stare haso:
            <Input
              value={password}
              onChange={passwordHandler}
              placeholder="old password"
              marginTop="1em"
              type="password"
            />
          </StyledLabel>
          <Button onClick={changeUserPassword}>Zmie</Button>
        </ModalWrapper>
      )}
    </Modal>
  );
};

SettingsModals.propTypes = {};

export default SettingsModals;
