import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { Form, ValidationMessage } from './LoginForm.styles';
import Input from 'components/atoms/Input/Input';
import Button from 'components/atoms/Button/Button';
import { AppContext } from 'store/Store';
import { login, getCurrentUser } from 'helpers/auth.service';
import { validateLogin } from 'helpers/loginValidation';

const LoginForm = ({ closeLoginBox }) => {
  const [message, setMessage] = useState('');
  const {
    username,
    usernameHandler,
    password,
    passwordHandler,
    clearFormInputs,
    setCurrentUser,
  } = useContext(AppContext);

  const signInHandler = async (e) => {
    e.preventDefault();
    setMessage('');
    await login(username, password, setMessage);
    await setCurrentUser(getCurrentUser());
    clearFormInputs();
    closeLoginBox();

    //validateLogin(username, password, setMessage);
  };

  return (
    <Form onSubmit={signInHandler}>
      <Input
        onChange={usernameHandler}
        value={username}
        placeholder="Nazwa uytkownika"
        type="text"
      />
      <Input
        onChange={passwordHandler}
        value={password}
        placeholder="Haso"
        type="password"
      />
      {message && <ValidationMessage>{message}</ValidationMessage>}
      <Button type="submit">ZALOGUJ SI</Button>
    </Form>
  );
};

LoginForm.propTypes = {
  closeMenu: PropTypes.func,
};

export default LoginForm;
