import axios from 'axios';
import authHeader from 'helpers/auth-header';

const requestBoard = axios.create({
  baseURL: 'http://localhost:8080/archive/api/v1/test/',
  validateStatus: false,
});

export const getPublicContent = async () => {
  return await requestBoard.get('all');
};

export const getUserBoard = async () => {
  return await requestBoard.get('user', { headers: authHeader() });
};

export const getModeratorBoard = async () => {
  return await requestBoard.get('mod', { headers: authHeader() });
};

export const getAdminBoard = async () => {
  return await requestBoard.get('admin', { headers: authHeader() });
};

const requestUserChanges = axios.create({
  baseURL: 'http://localhost:8080/archive/api/v1/users/',
  validateStatus: false,
  headers: authHeader(),
});

export const changeEmail = async (
  newEmail,
  password,
  username,
  handleSuccess,
  handleError
) => {
  return await requestUserChanges
    .put('email', {
      newEmail,
      password,
      username,
    })
    .then((response) => {
      if (response.data.username) {
        let user = JSON.parse(localStorage.getItem('user'));
        user.email = response.data.email;
        localStorage.setItem('user', JSON.stringify(user));
        handleSuccess();
        console.log(response);
      } else {
        handleError();
        console.log(response.data.message);
      }
    });
};

export const changePassword = async (newPassword, password, username, handleSucces, handleError) => {
  return await requestUserChanges
    .put('password', {
      newPassword,
      password,
      username,
    })
    .then((response) => {
      if (response.status === 200) {
        handleSucces();
        console.log(response);
      } else {
        handleError();
        console.log(response.data.message);
      }
      
    });
};
