import styled from 'styled-components';

export const SlideInfoWrapper = styled.div`
  width: 90vw;
  padding: 10px;
  border-radius: 15px;
  background-color: ${({ theme: { colors }, typeOfMessage }) => {
    if (typeOfMessage === 'WARNING') return colors.warning;
    if (typeOfMessage === 'ERROR') return colors.error;
    return colors.success;
  }};
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 3px 8px 3px rgb(0, 0, 0, 0.1);
  position: fixed;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1500;
  ${({theme}) => theme.media.desktop} {
    width: 70vh;
  }
`;

export const StyledSpan = styled.span`
  font-size: ${({ theme }) => theme.fontSize.m};
  color: ${({ theme }) => theme.colors.lightGrey};
  text-align: center;
  ${({theme}) => theme.media.desktop} {
    font-size: ${({ theme }) => theme.fontSize.s};
  }
`;
