import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { SlideInfoWrapper, StyledSpan } from './SlideInfo.style';
import { slideInfoAnimation } from 'assets/animations/animations';

const SlideInfo = ({ message, infoType, isInitiated, restartSliderState }) => {
  let slideBox = useRef(null);

  useEffect(() => {
    if (isInitiated) {
      slideInfoAnimation(slideBox);
      restartSliderState();
    }
  }, [isInitiated]);
  return (
    <SlideInfoWrapper ref={(el) => (slideBox = el)} typeOfMessage={infoType}>
      <StyledSpan>{message}</StyledSpan>
    </SlideInfoWrapper>
  );
};

SlideInfo.propTypes = {
  infoType: PropTypes.string,
};

export default SlideInfo;
